body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  font-size: 16px;
  font-family: monospace;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {box-sizing: border-box }
.App {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.container {
  width: 96%;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-align-self: center;
          align-self: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1%;
}

button {
  cursor: pointer;
}

@-webkit-keyframes fadeout {
  from { opacity : 1; }
  to { opacity : 0; }
}

@keyframes fadeout {
  from { opacity : 1; }
  to { opacity : 0; }
}

.btn {
  padding: 10px 20px;
  margin: 150px 0;
  border-radius: 15px;
  font-size: 1.2em;
  border: 4px double #ff5d42;
  background: none;
  color: white;
  -webkit-transition: background 500ms;
  transition: background 500ms;
  font-family: inherit;
}

.btn:hover {
  background: #ff5d42 !important;
}
.Instructions {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: none;
}

.Instructions-content {
  position: absolute;
  min-height: auto;
  padding: 20px;
  background: #ff5d42;
  border-radius: 15px;
  height: auto;
  top: 80px;
  -webkit-animation: dropdown 400ms;
          animation: dropdown 400ms;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  line-height: 1.6em;
}

@-webkit-keyframes dropdown {
  from { top: -1000px; }
  to { top: 60px; }
}

@keyframes dropdown {
  from { top: -1000px; }
  to { top: 60px; }
}

@-webkit-keyframes pullup {
  from { top: 60px; }
  to { top: -1000px; }
}

@keyframes pullup {
  from { top: 60px; }
  to { top: -1000px; }
}

.Instructions h2 {
  text-align: center;
}
.Title {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  min-height: 100vh;
  color: white;
  line-height: 1.4em;
}

.Title h1 {
  font-size: 3.5em;
}

.Title span {
  color: #ff5d42;
}

.Title h2 {
  font-size: 1.2em;
}
.FightStart {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.FightStart h1 {
  text-align: center;
  color: white;
  text-transform: uppercase;
  font-size: 5rem;
  margin-top: 200px;
  -webkit-animation: fadeout 4s;
          animation: fadeout 4s;
}
.Message {
  color: orange;
  text-align: center;
  padding: 10px 0;
  margin: 0;
}

#speaker-name {
  font-weight: bold;
}

.Message-Player {
  color: yellow;
}
#messages {
  border-bottom: 1px solid white;
  min-height: 100px;
  margin-bottom: 20px;
}
.Choice {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  color: white;
  background: none;
  border: none;
  font-size: inherit;
  font-family: inherit;
  text-align: left;
  padding: 5px 0;
  margin: 5px 0;
}

.Choice-hovered-choice {
  width: 10px;
  height: 10px;
  background-color: white;
  border-radius: 50%;
  margin-right: 10px;
  visibility: hidden;
}

.Choice-text {
  width: 96%;
}

.Choice:disabled,
.Choice:hover:disabled {
  color: grey;
  font-weight: normal;
  font-weight: initial;
}

.Choice:hover {
  font-weight: bold;
  color: yellow;
}

.Choice:hover .Choice-hovered-choice {
  visibility: visible;
}


@media screen and (min-width: 768px) {
  .Choice-hovered-choice {
    width: 10px;
    height: 10px;
  }
}
#player-choices {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding-top: 10px;
}
::-webkit-scrollbar {
    width: 1em;
}

::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}

::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey;
}
.EndExchange {
  text-align: center;
  color : #FFA500;
  border: 1px solid #FFA500;
  margin: 6px 0;
  font-size: 1.1em;
}

.EndExchange-player-win {
  color : yellow;
  border-color: yellow;
}
.FightEnd {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding-top: 90px;
}

.FightEnd p {
  font-size: 2em;
  padding: 0;
  text-transform: capitalize;
}

.FightEnd button {
  margin: 40px auto;
}
.Win {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.Win h1 {
  text-align: center;
  font-size: 4em;
  margin: 20px 0;
}

.Win p {
  font-size: 1.2em;
}
.Debug {
  background: #ff5d42;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  position: fixed;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  left: 10px;
  top: 0;
  padding: 10px;
  margin: 20px 0;
}

.Debug button {
  margin: 5px 10px;
  padding: 5px 7px;
  width: 100%;
  font-weight: bold;
}

.Game {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

#Game-instructions-btn {
  z-index: 2;
  margin: 15px auto;
  justify-self: flex-end;
  -webkit-align-self: flex-end;
          align-self: flex-end;
  max-width: 250px;
  background: black;
}

@media screen and (min-width: 768px) {
  #Game-instructions-btn {
    margin: 20px 20px 60px;
  }
}
.Info {
  text-align: center;
  padding: 40px 0;
  line-height: 1.6em;
  font-size: 0.9em;
}

.Info a {
  color: #ff5d42;
  /* text-decoration: none; */
  font-weight: bold;
  font-size: 1em;
}

.Info span a {
  color: white;
}

.Info span a:hover {
  color: #ff5d42;
}
